.backimg {
    background-image: url(https://img.freepik.com/free-photo/abstract-technology-binary-code-background_34629-592.jpg);
    background-size: cover;
    background-repeat: no-repeat;
}

#projectContainer .marginTop {
    margin-top: "10px";
}

html {
    height: fill-available;
    font-size: 15px;
  }
  
  body{
    margin: 0%;
    padding: 0%;
    min-height: 100vh;
    min-height: fill-available;
  }
  body::-webkit-scrollbar {
    display: none;
  }
  
  .app{
    display: flex;
    flex-direction: column;
  }
  
  #introduce,#events,#teams{
    border-bottom: 1px white solid;
  }
  
  #introduce{
    height: 100vh;
    background-color: black;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  
  #events{
    height: 100vh;
    width: 100%;
    background-color: black;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
  }
  
  #events .row{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    height: 50%;
    color: white;
  }
  
  #events .row div{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    width: 33%;
    font-family: 'Anton', sans-serif;
    font-size: 4rem;
    cursor: pointer;
    background-position: center;
    background-size: cover;
  }
  
  .title, .content{
    opacity: 0;
  }
  
  #events .row div p{
    font-size: 1.5rem;
    font-family: 'PT Serif', serif;
  }
  
  #events .row div:hover{
    background-position: center;
    background-size: 150%;
    background-repeat: no-repeat;
  }
  
  #events .row div:hover .title{
    opacity: 1;
  }
  
  #events .row div:hover .content{
    opacity: 1;
  }
  
  .artlab, .skydays, .akademi, .gecekodu, .bizbize, .isebak{
    position: relative;
  }
  
  .artlab::before{
    content: ' ';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 1;
    background-image: url('./Components/View/artlab.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  
  .skydays::before{
    content: ' ';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 1;
    background-image: url('./Components/View/skydays.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  
  .akademi::before{
    content: ' ';
    display: block;
    position: absolute;
    left: 0;
