    width: 100%;
    height: 100%;
    opacity: 1;
    background-image: url('./Components/View/akademi.JPG');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  
  .bizbize::before{
    content: ' ';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 1;
    background-image: url('./Components/View/bizbize.JPG');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  
  .gecekodu::before{
    content: ' ';
    display: block;
