    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 1;
    background-image: url('./Components/View/gecekodu.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  
  .isebak::before{
    content: ' ';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 1;
    background-image: url('./Components/View/skydays.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  
  #events .row div:hover::before{
    opacity: 0.5;
    background-size: 150%;
    background-repeat: no-repeat;
    background-position: center;
  }
  
  .event-content{
    position: relative;
  }
  
  #teams{
    height: 100vh;
    background-color: black;
    display: flex;
    box-sizing: border-box;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: white;
  }
  
  #navbar{
    position: fixed;
    width: 100%;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 3;
  }
  
  .content1{
    margin-left: 4rem;
    color: white;
    width: 50%;
  }
  
  .header{
    font-family: 'PT Serif', serif;
    font-size: 4rem;
  }
  
  .introduce{
    font-family: 'PT Serif', serif;
    font-size: 1.5rem;
    margin-top: 2rem;
  }
  
  a{
    margin: 0.8rem;
    text-decoration-line: none;
    font-family: 'PT Serif', serif;
    font-weight: bold;
  }
  
  .navselectedTeam{
    border-bottom: 1px solid white;
  }
  
  .navTeam{
    padding : 0.8rem;
    cursor: pointer;
  }
  
  .navTeam:hover{
    opacity: 0.8;
  }
  
  .bigfont{
    font-size: 2rem;
    width: max-content;
  }
  
  .horizontalCenter {
  margin: auto;}
  
  .pointer{
    cursor:pointer;
  }
  
  .gameContext{
    padding-top: 20vh;
  }
  
  .invisible {
    display: none;
  }
  
  .space{
    white-space: pre-line;
  }
  
  .middleBigFont{
    font-size: 1.5rem;
  }
  
  .wrapper {
    position: relative;
    transform: translateY(100%);
    animation: 20s credits linear;
    animation-fill-mode: forwards;
  }
  
  .gamelab{
    font-family: 'PT Serif', serif;
    font-size: 2.5rem;
  }
  
  .header-team{
    font-size: 2.8rem;
  }
  
  .description-team{
    font-size: 1.3rem;
    width: 60%;
    margin-inline: auto;
    box-sizing: border-box;
    height: 50%;
  }
  
  #bottom{
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 100vh;
    background-color: black;
  }
  
  #bottom button{
    width: 100%;
    height: 100%;
    background-color: black;
    color: white;
  }
  
  #bottom button:hover{
    background-color: white;
    color: black;
  }
  
  #dots i{
    cursor: pointer;
    margin-inline: 3px;
  }
  
  #dots{
    margin: 0.7rem;
  }
  
  #bottombar{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    color: white;
    text-align: center;
  }

  #loginpage{
  }

  @media screen and (max-width : 1000px) {
    .yildiz{
      width: 400px;
    }
    html{
      font-size: 13px;
    }
    #events {
      height: 100vh;
      width: 100%;
      background-color: black;
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
  }
  #events .row div{
    width: 100%;
  }
    /* resimler */
  }

  @media screen and (max-width : 750px) {
    .event-content .title{
      font-size: 40px;
    }
    .yildiz{
      width: 300px;
    }
    html{
      font-size: 12px;
    }
  }

  @media screen and (max-width : 650px) {
    .navTeam{
      font-size: 8px;
    }
    .yildiz{
      width: 200px;
    }
    html{
      font-size: 11px;
    }
  }

  @media screen and (max-width : 450px) {
    .navTeam{
      font-size: 8px;
    }
    .yildiz{
      width: 150px;
    }
    #introduce{
      flex-direction: column-reverse;
      justify-content: center;
    }
    html{
      font-size: 10px;
    }
  }

  @media screen and (max-width : 400px) {
    .navTeam{
      font-size: 8px;
    }
    .yildiz{
      width: 150px;
    }
    #introduce{
      flex-direction: column-reverse;
      justify-content: center;
    }
    html{
      font-size: 8px;
    }
  }